'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';
import '../css/order.css';


var appBasePath = globalData.appBasePath;

var Order=React.createClass({
    getInitialState:function(){
        return {checked: false}
    },
    handleClick:function(){
        this.setState({
            checked: !this.state.checked
        })
    },
    render:function(){
        var self = this;
        var backRouter=this.props.params.backRouter;
        var btnStyle = {
            backgroundColor: self.state.checked ? '#ddd' : '#53a6ff'
        }
        return (
            <div className="app_Box orderList">
                <Header title="" backRouter={backRouter}/>
                <ul>
                    <li>
                        <div className="orderNum">
                            201705092356412
                            <span></span>
                        </div>
                        <h3 className="list_title">
                            <img src="src/img/icon/order.png" />
                            <span></span>
                            <span className="p_name"></span>
                        </h3>
                        <ul className="container">
                            <li> 5000</li>
                            <li>12</li>
                            <li>480</li>
                            <li>3.17%</li>
                        </ul>
                        <div className="listFoot">
                            <span className="status">3</span>
                            <span className="statusBtn" onClick={this.handleClick} style={btnStyle}></span>
                        </div>
                    </li>
                    <li>
                        <div className="orderNum">
                            201705092356412
                            <span></span>
                        </div>
                        <h3 className="list_title">
                            <img src="src/img/icon/order.png" />
                            <span></span>
                            <span className="p_name"></span>
                        </h3>
                        <ul className="container">
                            <li> 5000</li>
                            <li>12</li>
                            <li>480</li>
                            <li>3.17%</li>
                        </ul>
                        <div className="listFoot">
                            <span className="status">3</span>
                            <span className="statusBtn" onClick={this.handleClick} style={btnStyle}></span>
                        </div>
                    </li>
                </ul>
                
            </div>
        )
    }
})
export default Order; 



